import React , {useState , useRef, useEffect }from 'react'
import Loader from '../../common/loader/Loader'
import { ToastContainer } from 'react-toastify'
import { usePayoutContext } from './PayoutProvider'
import {
  Box,
  Button,
  Card,
  CardActionArea,
  TextField,
  CardContent,
  Grid,
  Typography,
  IconButton,
    CircularProgress,
    Paper,
  Chip,
  MenuItem,
  Stack,
  Autocomplete,
} from '@mui/material';
import { CheckCircle } from '@mui/icons-material';
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';
import { getAllBank , singlePayout } from '../../api/login/Login';
import withLocation from '../../utils/withLocation';
import toastMessage from '../../utils/toastMessage';

const CustomTextField = (props) => (
  <TextField
    // variant="outlined"
    size="small"
    InputLabelProps={{
      shrink: true,
    //   style: { fontSize: '16px' },
    }}
    sx={{
    
      "& label.Mui-focused": {
        color: '#6c757d',
      },
        "& .MuiOutlinedInput-root": {
            height: 40, 
            "& .MuiOutlinedInput-input": {
                py: 0,
              },
          "&:hover fieldset": { borderColor: "#6c757d" },
          "&.Mui-focused fieldset": { borderColor: "#6c757d" },
        },
     
    }}
    {...props}
  />
);


const CustomBox =  ({children})=> (
    <Box
    sx={{
      width: '100%',
    //   maxWidth: 800,
      mx: 'auto',
    //   mt: 5,
    my:1,
      px: 3,
      py:4,
      pb:6,
    //   bgcolor: '#F8F9FA',
    bgcolor:'white',
      borderRadius: 1,
      boxShadow: 0,
    }}
  >
    {children}
    </Box>
)


const StartPage = () => {
  const { setCurrentPage } = usePayoutContext();
  const [selectedType, setSelectedType] = useState('');

  const options = [
    { id: 'single', name: 'Single Payout' },
    { id: 'bulk', name: 'Bulk Payout' },
  ];

  const handleSubmit = () => {
    if (selectedType === 'single') setCurrentPage('single-payout');
    else if (selectedType === 'bulk') setCurrentPage('bulk-payout');
  };

  return (
    <>
       <div className="PageHeading">
        <h1>Payout Service</h1>
      </div>

      <CustomBox>
        <Typography variant="h6" fontWeight="500" mb={2}>
          Select Payout Type
        </Typography>

        <Grid container spacing={2} justifyContent="flex-start">
          {options.map((option) => {
            const isSelected = selectedType === option.id;

            return (
              <Grid item xs={6} sm={3} md={3} lg={3} key={option.id}>
                <Card
                  elevation={isSelected ? 6 : 1}
                  sx={{
                    border: isSelected ? '1.5px solid var(--bs-nav)' : '1px solid #ccc',
                    position: 'relative',
                    borderRadius: 2,
                    transition: 'all 0.3s ease',
                    // width: '200px',
                  }}
                >
                  <CardActionArea onClick={() => setSelectedType(option.id)}>
                    <CardContent sx={{ p: 1 }}>
                      <Typography
                        variant="subtitle1"
                        align="center"
                        color="var(--bs-nav)"
                        fontSize={14}
                      >
                        {option.name.toUpperCase()}
                      </Typography>
                    </CardContent>
                    {/* {isSelected && (
                      <CheckCircle
                        sx={{
                          position: 'absolute',
                          top: 11,
                          right: 44,
                          width: '17px',
                          height: '17px',
                          fill: 'var(--bs-nav)',
                        }}
                      />
                    )} */}
                  </CardActionArea>
                </Card>
              </Grid>
            );
          })}
        </Grid>

        {/* Submit Button */}
        <Box textAlign="center" mt={4}>
          <Button
            variant="contained"
            color="primary"
            onClick={handleSubmit}
            disabled={!selectedType}
               sx={{
            height: 40,
            borderColor: 'var(--bs-primary1)', // Custom colors
            backgroundColor: 'var(--bs-primary1)', // Custom colors
            color: "white",

          }}
          >
            Continue to {selectedType === 'bulk' ? 'Bulk Payout' : 'Single Payout'}
          </Button>
        </Box>

        {/* Descriptions */}
        <Box mt={5}>
          <Typography variant="body2" color="var(--bs-nav)" mb={1}>
            <strong>Single Payout:</strong> To Proceed with a single Payout , Kindly select this option.
          </Typography>
          <Typography variant="body2" color="var(--bs-nav)">
            <strong>Bulk Payout:</strong> To Proceed with a bulk Payout , Kindly select this option.
          </Typography>
        </Box>
      </CustomBox>
    </>
  );
};



const SinglePayoutScreen = () => {
  const { setCurrentPage , setLoader } = usePayoutContext();
   const [bankList, setBankList] = useState([]);

  

  const [formData, setFormData] = useState({
    customer_mobile: '',
    receiver_name: '',
    ifsc_code: '',
    bank_name: null,
    account_number: '',
    sender_name: '',
    amount: '',
    remarks: '',
    account_type: '',
    txn_mode: '',
    beneficiary_email: '',
  });

  const [formErrors, setFormErrors] = useState({});

const handleChange = (e) => {
  const { name, value } = e.target;

  const trimmedValue = value.trimStart(); // prevent leading spaces

  if (name === 'amount') {
    const regex = /^\d*\.?\d{0,2}$/;
    if (trimmedValue === '' || regex.test(trimmedValue)) {
      setFormData((prev) => ({ ...prev, [name]: trimmedValue }));
    }
  } else if (name === 'customer_mobile' || name === 'account_number') {
    const cleanedValue = trimmedValue.replace(/\D/g, '');
    setFormData((prev) => ({ ...prev, [name]: cleanedValue }));
  } else {
    setFormData((prev) => ({ ...prev, [name]: trimmedValue }));
  }

  setFormErrors((prev) => ({ ...prev, [name]: '' }));
};


const validate = () => {
  const errors = {};

  if (!formData.customer_mobile || formData.customer_mobile.trim().length !== 10) {
    errors.customer_mobile = 'Enter valid 10-digit mobile number';
  }

  if (!formData.receiver_name.trim()) {
    errors.receiver_name = 'Receiver name is required';
  }

  if (!formData.bank_name) {
    errors.bank_name = 'Bank name is required';
  }

  if (!formData.account_number.trim()) {
    errors.account_number = 'Account number is required';
  }

  if (!formData.amount || isNaN(formData.amount)) {
    errors.amount = 'Enter a valid amount';
  }

  if (!formData.account_type) {
    errors.account_type = 'Select account type';
  }

  if (!formData.txn_mode) {
    errors.txn_mode = 'Select transaction mode';
  }

  if (formData.beneficiary_email.trim()) {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(formData.beneficiary_email.trim())) {
      errors.beneficiary_email = 'Enter a valid email';
    }
  }

    const ifscRegex = /^[A-Z]{4}0[A-Z0-9]{6}$/;
  if (!formData.ifsc_code || !formData.ifsc_code.trim()) {
    errors.ifsc_code = 'IFSC code is required';
  } else if (!ifscRegex.test(formData.ifsc_code.trim().toUpperCase())) {
    errors.ifsc_code = 'Enter a valid IFSC code';
  }

  setFormErrors(errors);
  return Object.keys(errors).length === 0;
};


 const handleSubmit = withLocation(async (latitude, longitude, e) => {
  

  if (validate()) {
    const payload = {
      ...formData,
      latlong: `${latitude.toFixed(4)},${longitude.toFixed(4)}`,
      bank_name : formData.bank_name.name
    };

    try {
      setLoader(true);
      // const res = {}
      const res = await singlePayout(payload); 
      // handle success response
       toastMessage(res?.data?.error , res?.data?.message)
      if(res?.data?.error === false)
      {
         setFormData(
          {
    customer_mobile: '',
    receiver_name: '',
    ifsc_code: '',
    bank_name: null,
    account_number: '',
    sender_name: '',
    amount: '',
    remarks: '',
    account_type: '',
    txn_mode: '',
    beneficiary_email: '',
  }
         )
      }

    } catch (error) {
      console.error('Payout error:', error);
    } finally {
      setLoader(false);
    }
  }
});



const handleFormSubmit = (e) => {
  e.preventDefault(); 
  handleSubmit(e);    
}


    useEffect(() => {
    const fetchBanks = async () => {
      try {
        const res = await getAllBank(); // Call your API
        const mapped = res?.data?.data?.map((item) => ({
          value: item,
          label: item.bank_name,
          _id: item._id,
          name: item.bank_name, // for rendering
        }));
        setBankList(mapped);
      } catch (error) {
        console.error("Error fetching bank list:", error);
      }
    };
    fetchBanks();
  }, []);

   const handleBankChange = (event, newValue) => {
   
    setFormData((prev) => ({
      ...prev,
      bank_name: newValue,
    }));
    setFormErrors((prev) => ({ ...prev, bank_name: '' }));
  };

  return (
    <>

    
      <div className="PageHeading">
        <h1>Payout Service</h1>
      </div>

      <CustomBox>

        <Box component="form" onSubmit={(e) => handleFormSubmit(e)}>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <CustomTextField
                fullWidth
                label="Customer Mobile"
                name="customer_mobile"
                value={formData.customer_mobile}
                onChange={handleChange}
                required
                 inputProps={{ maxLength: 10 }}
                error={!!formErrors.customer_mobile}
                helperText={formErrors.customer_mobile}
                size="small"
              
              />
            </Grid>

            <Grid item xs={12} sm={6}>
              <CustomTextField
                fullWidth
                label="Receiver Name"
                name="receiver_name"
                value={formData.receiver_name}
                onChange={handleChange}
                required
                error={!!formErrors.receiver_name}
                helperText={formErrors.receiver_name}
                size="small"
              />
            </Grid>

        <Grid item xs={12} sm={6}>
  <Autocomplete
    options={bankList}
    value={formData.bank_name}
    onChange={handleBankChange}
    getOptionLabel={(option) => option?.label || ''}
    isOptionEqualToValue={(option, value) => option?._id === value?._id}
    fullWidth
    size="small"
    renderOption={(props, option) => (
      <li {...props} key={option._id}>
        <Box
          sx={{ fontSize: '13px', color: '#000000' }}
          display="flex"
          columnGap={1.5}
          alignItems="center"
        >
          {option.name}
        </Box>
      </li>
    )}
    renderInput={(params) => (
     <CustomTextField
    {...params}
    required
    label="Bank Name" // 
    error={!!formErrors.bank_name}
    helperText={formErrors.bank_name}
    fullWidth
    size="small"
     InputLabelProps={{ shrink: true }} // ✅ shrink enabled
  />
    )}
  />
</Grid>

<Grid item xs={12} sm={6}>
  <CustomTextField
    fullWidth
    label="IFSC Code"
    name="ifsc_code"
    value={formData.ifsc_code || ''}
    onChange={handleChange}
    required
    size="small"
    error={!!formErrors.ifsc_code}
                helperText={formErrors.ifsc_code}
  />
</Grid>


            <Grid item xs={12} sm={6}>
              <CustomTextField
                fullWidth
                label="Account Number"
                name="account_number"
                value={formData.account_number}
                onChange={handleChange}
                required
                error={!!formErrors.account_number}
                helperText={formErrors.account_number}
                size="small"
              />
            </Grid>

            <Grid item xs={12} sm={6}>
              <CustomTextField
                fullWidth
                label="Sender Name"
                name="sender_name"
                value={formData.sender_name}
                onChange={handleChange}
                size="small"
                required
              />
            </Grid>

            <Grid item xs={12} sm={6}>
              <CustomTextField
                fullWidth
                label="Amount"
                name="amount"
                type="text"
                value={formData.amount}
                onChange={handleChange}
                required
                error={!!formErrors.amount}
                helperText={formErrors.amount}
                size="small"
              />
            </Grid>

            <Grid item xs={12} sm={6}>
              <CustomTextField
                fullWidth
                label="Remarks"
                name="remarks"
                value={formData.remarks}
                onChange={handleChange}
                multiline
                required
                size="small"
              />
            </Grid>

            <Grid item xs={12} sm={6}>
              <CustomTextField
                select
                fullWidth
                label="Account Type"
                name="account_type"
                value={formData.account_type}
                onChange={handleChange}
                size="small"
                required
                error={!!formErrors.account_type}
                helperText={formErrors.account_type}
              >
                <MenuItem sx={{color:'#000000' , fontSize:13}} value="1">Savings</MenuItem>
                <MenuItem sx={{color:'#000000' , fontSize:13}} value="2">Current</MenuItem>
              </CustomTextField>
            </Grid>

            <Grid item xs={12} sm={6}>
              <CustomTextField
                select
                fullWidth
                label="Transaction Mode"
                name="txn_mode"
                value={formData.txn_mode}
                onChange={handleChange}
                size="small"
                required
                error={!!formErrors.txn_mode}
                helperText={formErrors.txn_mode}
              >
                <MenuItem sx={{color:'#000000' , fontSize:13}} value="imps">IMPS</MenuItem>
                <MenuItem sx={{color:'#000000' , fontSize:13}} value="neft">NEFT</MenuItem>

              </CustomTextField>
            </Grid>

            <Grid item xs={12} sm={6}>
              <CustomTextField
                fullWidth
                label="Beneficiary Email"
                name="beneficiary_email"
                value={formData.beneficiary_email}
                onChange={handleChange}
                size="small"
                required
                error={!!formErrors.beneficiary_email}
                helperText={formErrors.beneficiary_email}
              />
            </Grid>
          </Grid>

          <Box mt={4} textAlign="center">
            <Button
              type="submit"
              variant="contained"
              sx={{
                backgroundColor: '#6C757D',
                color: '#fff',
                textTransform: 'none',
                fontWeight: 600,
                width:'200px',
                fontSize: 14,
                '&:hover': {
                  backgroundColor: '#6C757D',
                  opacity: 0.9,
                },
              }}
            >
              Proceed
            </Button>
          </Box>
        </Box>
      </CustomBox>
    </>
  );
};

const BulkPayoutScreen = ()=>{
   const { setCurrentPage } = usePayoutContext();
  const fileInputRef = useRef();
  const [uploading, setUploading] = useState(false);
  const [uploadResult, setUploadResult] = useState(null);
  const [selectedFile, setSelectedFile] = useState(null);

  const handleDownloadFormat = () => {
    const link = document.createElement('a');
    link.href = '/sample-format.xlsx';
    link.download = 'Bulk_Payout_Format.xlsx';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  const handleFileUpload = async (e) => {
    const file = e.target.files[0];
    if (!file) return;

    setSelectedFile(file);
    setUploading(true);
    setUploadResult(null);

    try {
      await new Promise((res) => setTimeout(res, 2000)); // mock
      setUploadResult({
        success: 10,
        pending: 0,
        failed: 0,
        total: 10,
      });
    } catch (err) {
      console.error(err);
    } finally {
      setUploading(false);
    }
  };

  return (
    
  
    <>
      <div className="PageHeading">
        <h1 >Bulk Payout Service</h1>
      </div>

      <CustomBox>
        {/* Header */}
        <Box display="flex" alignItems="center" gap={2} mb={3}>
          <IconButton onClick={() => setCurrentPage('start')} sx={{ color: 'var(--bs-nav)' }}>
            <ArrowBackIosNewIcon />
          </IconButton>
          <Typography
            variant="h6"
            fontWeight={500}
            sx={{ color: 'var(--bs-nav)', fontSize: 20 }}
          >
            Upload Excel File
          </Typography>
        </Box>
         <Box mt={2} textAlign="right">
          <Button
            // variant="outlined"
            onClick={handleDownloadFormat}
            
            sx={{
               color: 'var(--bs-primary1)',
              borderColor: 'var(--bs-primary1)',
              fontSize: 14,
              textTransform: 'none',
              mb:2,
            }}
          >
            Download Excel Format
          </Button>
        </Box>
        {/* Upload Box */}
        <Box
          sx={{
            position: 'relative',
            width: '100%',
            height: 200,
            borderRadius: 2,
            overflow: 'hidden',
            bgcolor: 'grey.200',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            flexDirection: 'column',
            
            border: '1px solid var(--bs-nav)',
          }}
          
        >
          <input
            ref={fileInputRef}
            type="file"
            accept=".xls,.xlsx"
            hidden
            onChange={handleFileUpload}
          />
          <Button
            // variant="outlined"
            sx={{
             
               color: 'var(--bs-nav)',
              borderColor: 'var(--bs-nav)',
              fontSize: 14,
              textTransform: 'none',
            }}
            disabled={uploading}
            onClick={() => fileInputRef.current.click()}
          >
            {uploading ? <CircularProgress size={20} /> : 'Upload Excel File'}
          </Button>

          {selectedFile && (
            <Typography mt={2} fontSize={13} color="textSecondary">
              Selected File: <strong>{selectedFile.name}</strong>
            </Typography>
          )}
        </Box>

        {/* Download Format */}
       

        {/* Upload Result Summary */}
        {uploadResult && (
          <Paper
            elevation={0}
            sx={{
              mt: 4,
              p: 3,
              bgcolor: '#f9f9f9',
              borderRadius: 2,
            }}
          >
            <Typography
              variant="subtitle1"
              fontWeight={600}
              sx={{ color: 'var(--bs-nav)', fontSize: 16, mb: 2 }}
            >
              Transaction Summary
            </Typography>

          <Stack direction="row" spacing={2} mb={2}>
  <Chip
    label={
      <Typography sx={{ fontSize:14, color: '#fff', fontWeight: 500 }}>
        Success: {uploadResult.success}
      </Typography>
    }
    sx={{ bgcolor: 'green', px: 1.5, py: 0.5 }}
  />
  <Chip
    label={
      <Typography sx={{ fontSize:14, color: '#fff', fontWeight: 500 }}>
        Pending: {uploadResult.pending}
      </Typography>
    }
    sx={{ bgcolor: '#f0ad4e', px: 1.5, py: 0.5 }}
  />
  <Chip
    label={
      <Typography sx={{ fontSize:14, color: '#fff', fontWeight: 500 }}>
        Failed: {uploadResult.failed}
      </Typography>
    }
    sx={{ bgcolor: '#d9534f', px: 1.5, py: 0.5 }}
  />
</Stack>


            <Typography variant="body2" fontSize={14}>
              <strong style={{ color: 'var(--bs-nav)' }}>Total Records:</strong>{' '}
              {uploadResult.total}
            </Typography>
          </Paper>
        )}
      </CustomBox>
    </>
  
  );
}



const PayoutService = () => {

   const { currentPage ,  loader  } =   usePayoutContext()
  return (
    <>
   
          {loader && <Loader />}
        
         <SinglePayoutScreen/>
          
          {/* { currentPage === 'start' && <StartPage/>}

          {currentPage ==='single-payout' && <SinglePayoutScreen/>}

          {currentPage ==='bulk-payout' && <BulkPayoutScreen/>} */}
    
    
    </>
  )
}

export default PayoutService